@import url(https://fonts.googleapis.com/css?family=Poppins:500,600&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.header {
    background-color: #F8054D;
    text-align: center;
}

.description {
    display: flex;
    align-items: center;
    color: white;
    margin: 0 0 0 50px;
}

.description .separator {
    width: 2px;
    height: 60px;
    background: white;
    margin: 0 20px;
}

.copy {
    width: 100%;
    text-align: center;
    padding: 30px 0;
    color: white;
    opacity: .5;
}

.main {
    display: inline-flex;
    padding: 8em 0;
}

.main img {
    height: 15em;
}

.description h1 {
    font-size: 4.5em;
}

.description strong {
    font-size: 1.5em;
}

.social-links {
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
}

.social-links>a>li>svg {
    height: 1.5em;
    margin: 1em;
    opacity: 0.5;
    -webkit-transition: opacity .08s;
    transition: opacity .08s;
}

.social-links>a:hover>li>svg {
    opacity: 1;
}

@media (max-width: 1000px) {
    .social-links {
        margin-top: 3em;
    }
}
.header-mobile {
    padding: 5em 0 2em 0;
    background-color: #F8054D;
    text-align: center;
}

.mobile-content > img {
    height: 15em;
}

.mobile-description {
    color: white
}

.mobile-description > h1 {
    font-size: 3em;
}

.mobile-description > strong {
    font-size: 1em;
}
.desktop-options {
    display: inline-flex;
    list-style: none;
    margin-left: -2em;
}

.desktop-options>li {
    margin: 0 1.5em;
    padding: 1em 0;
}

.desktop-options>li>a {
    text-decoration: none;
    color: black;
}

.selected {
    border-bottom: 2px solid #F8054D;
}

.desktop-options-menu {
    text-align: center;
    margin: 1em 0;
}
.mobile-menu-icon {
    height: 1.5em;
    float: right;
    margin: 1.5em 0;
    opacity: .25;
}

.mobile-selected {
    float: left;
    display: flex;
    align-items: center;
    height: 2.5em;
}

.menu-mobile {
    padding: .5em 10%;
    height: 4.5em;
}

/* context options */

.mobile-context {
    border-bottom: 1px solid #ececec;
    margin-bottom: 1.5em;
}

.mobile-context-options {
    list-style: none;
}

.mobile-context-options>li {
    padding: 1.5em 0;
}

.mobile-context-options>li>a {
    text-decoration: none;
    color: black;
    opacity: .5;
}
.project-canvas {
    background-color: blue;
    height: 20em;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.project-canvas>img {
    height: 5em;
    -webkit-transition: height .3s;
    transition: height .3s;
}

.project-container:hover>.project-canvas>img {
    /*height: 6em;*/
}

.project-container:hover>.project-description {
    padding-right: 2.5em;
}

.project-description {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 3em;
    -webkit-transition: padding .2s;
    transition: padding .2s;
}

.project-description>img {
    height: 1em;
}

.project-description>p {
    margin-right: 1em;
}

.project-title-canvas>div>img {
    height: 5em;
}

.project-title-canvas>div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 15em;
    padding-top: 10em;
    padding-bottom: 6em;
}

.project-title-canvas {
    overflow: hidden;
    background-color: white;
}

.project-overlay {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 3em 15%;
    overflow-y: scroll;
}

.project-content-md {
    margin: 7% 10%
}

.project-image-canvas {
    background-size: cover;
    background-position: center;
    width: 100%;
}

.project-overlay-close {
    color: white;
    height: 1.5em;
    opacity: 0.5;
    margin: 1em;
    -webkit-transition: opacity .1s;
    transition: opacity .1s;
    cursor: pointer;
}

.project-overlay-close:hover {
    opacity: 1
}

.project-overlay-links {
    margin: 3em 0;
    display: flex;
    list-style: none;
    padding: 0;
}


.project-overlay-links>a {
    text-decoration: none;
    margin: .5em;
    padding: 1.1em 3em;
    background-color: white;
    color: #212121;
    font-size: 11pt;
    text-transform: capitalize;
}

@media (max-width: 800px) {
    .project-overlay {
        background-color: white;
        padding: 0
    }

    .project-overlay-close {
        position: absolute;
    }
}
.tech-stack {
    display: flex;
    list-style: none;
    justify-content:center;
    flex-wrap: wrap;
    padding: 0;
}

.tech-stack>li {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: .35;
    padding: 1em 0;
    flex-grow: 1;
    min-width: 20%;
}

.tech-stack>li>svg {
    color: #3b3b3b;
    height: 3em;
}

.tech-stack>li>p {
    color: #3b3b3b;
}

@media (max-width: 850px){
    .tech-stack>li {
        min-width: 50%;
    }
}
.main-new {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 23em;
}

.main-new>p {
    max-width: 350px;
    color: white;
    font-size: 2em;
}

.main-new-container>.links {
    color: white;
    list-style: none;
    display: flex;
    align-items: center;
    height: 5em;
    margin-left: 1.5em;
    margin-top: -2em;
}

.main-new-container>.links>a {
    color: white;
    text-decoration: none;
    margin-right: 3em;
    opacity: .5;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}

.main-new-container>.links>a:hover {
    opacity: 1;
}

.main-new>img {
    height: 10em
}

.separator-container {
    display: flex;
    align-items: center;
    height: 5em;
}

.separator-container>.title {
    padding: 10px;
    background-color: #F8054D;
    color: white;
    font-size: 1em;
    margin-right: 1em;
    text-transform: capitalize;
}

.separator-container>.subtitle {
    font-size: .9em;
}

.mini-news-list {
    margin-top: -1em;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.mini-news-list>li {
    min-width: 49%;
    flex-grow: 1;
}

.mini-new-container {
    height: 20em;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#00000080));
    background: linear-gradient(180deg, transparent, #00000080);
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}

.mini-new-container>h2 {
    color: white;
    margin: 1em
}

.mini-new-container>a {
    color: white;
    opacity: .5;
    text-decoration: none;
    margin: 1.7em;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}

.mini-new-container>a:hover {
    opacity: 1;
}

@media (max-width: 900px) {

    .separator-container {
        justify-content: space-between;
        margin: 0 1em;
    }

    .main-new {
        flex-direction: column;
        height: 28em;
        justify-content: center;
    }

    .main-new>p {
        font-size: 1.4em;
        text-align: center;
    }

    .main-new-container>.links {
        margin-top: 0;
        justify-content: center;
        margin-left: 0;
    }

    .main-new>img {
        height: 7em;
    }
}
.label-contact-link > a {
    text-decoration: none;
    color:white;
    padding: 0.3em 1.5em;
    text-decoration: underline;
}

.label-contact-link {
    display: flex;
    margin-bottom: .5em;
    width: 23em;
}
.contact-container {
    display: flex;
    justify-content: center;
    padding: 1em;
}

.contact-info-container {
    margin-right: 5vw;
    height: 20em;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.contact-container>.emote-field>p {
    font-size: 3em;
    line-height: 0%;
    font-weight: 900;


}

.contact-container>.emote-field>h2 {
    font-size: 2.5em;

}

.contact-container>.emote-field {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 20em;
    color: transparent;

    background: linear-gradient(45deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    -webkit-animation: rainbow_animation 8s ease-in-out infinite;
            animation: rainbow_animation 8s ease-in-out infinite;
    background-size: 1000% 1000%;
}

@-webkit-keyframes rainbow_animation {

    0% {
        background-position: 0% 82%
    }

    50% {
        background-position: 100% 19%
    }

    100% {
        background-position: 0% 82%
    }
}

@keyframes rainbow_animation {

    0% {
        background-position: 0% 82%
    }

    50% {
        background-position: 100% 19%
    }

    100% {
        background-position: 0% 82%
    }
}

@media (max-width: 1120px) {
    .contact-container {
        flex-direction: column-reverse;
    }
    .contact-container>.contact-info-container {
        align-items: center;
        text-align: center;
        margin: -3em 0;
    }

    .contact-container>.emote-field {
        height: 15em;
    }

    .label-contact-link {
        width: 100%;
    }
}
