.contact-container {
    display: flex;
    justify-content: center;
    padding: 1em;
}

.contact-info-container {
    margin-right: 5vw;
    height: 20em;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.contact-container>.emote-field>p {
    font-size: 3em;
    line-height: 0%;
    font-weight: 900;


}

.contact-container>.emote-field>h2 {
    font-size: 2.5em;

}

.contact-container>.emote-field {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 20em;
    color: transparent;

    background: linear-gradient(45deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    animation: rainbow_animation 8s ease-in-out infinite;
    background-size: 1000% 1000%;
}

@keyframes rainbow_animation {

    0% {
        background-position: 0% 82%
    }

    50% {
        background-position: 100% 19%
    }

    100% {
        background-position: 0% 82%
    }
}

@media (max-width: 1120px) {
    .contact-container {
        flex-direction: column-reverse;
    }
    .contact-container>.contact-info-container {
        align-items: center;
        text-align: center;
        margin: -3em 0;
    }

    .contact-container>.emote-field {
        height: 15em;
    }

    .label-contact-link {
        width: 100%;
    }
}